import {mapState} from 'vuex'
export default{
	data(){
		return{
			page:1,
			limit:8
		}
	},
	props:{
		goodMsg:{
			type:Object
		}
	},
	computed:{
		...mapState({
			sessionKey:state=>state.sessionKey
		})
	},
	methods:{
		remove(id){
			let that=this;
			this.$api.mydele({
				id:id,
				page:that.page,
				limit:that.limit,
				sessionKey:that.sessionKey
			}).then(res=>{
				that.goods=res.goods;
				uni.showToast({
					title:res.msg
				})
			})
		},
		toInfo(id){
			uni.navigateTo({
				url:'../../pages/Info/Info'+id
			})
		},
	}
}
